.controller-section {
    width: 100%;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}


.controller-section .section-box {
    width: 400px;
    height: 400px;
    border-radius: 50%;
    border: 1px solid #D7D8D8;
    background: #D7D8D8;
    /* Web/2 - Button WEB */

    box-shadow: 0px 2px 6px rgba(0, 0, 0, 0.05), 0px 1px 3px rgba(0, 0, 0, 0.15);
    position: relative;
}

.gourd-1{
    position: absolute;
    top: 30%;
    left: 20%;
    width: 80px;
    height: 80px;
    box-shadow: 0px 2px 6px rgba(0, 0, 0, 0.05), 0px 1px 3px rgba(0, 0, 0, 0.15);
    border: 1px solid #D7D8D8;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 10px;
}

.gourd-1 img{
    width: 100%;
    object-fit: cover;
    height: 100%;
}
